body{
  background-color: #66B98F;
}
.App{
  background-color: #E37222;
  min-width: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input{
  background-color: rgba(7,136,155);
  border: 0;
  width: 250px;
  height: 50px;
  padding: 0 20px;
  margin: 40px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.9);
}
#to-do-form input:focus, #to-do-form button:focus{
  outline:none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #f6f209;
  cursor: pointer;
  font-weight: bolder;
}